import { PageContainer, ProLayout, ProCard } from '@ant-design/pro-components';
import ProSkeleton from '@ant-design/pro-skeleton';
import { Button, Result } from 'antd';
import axios from 'axios';
import { ErrorInfo, useRef } from 'react';
import React, { useState } from 'react';
import Webcam from "react-webcam"

const ErrorTriggerTestPage = () => {
  // default to throw error for snapshot test
  const [error, setError] = useState<boolean>(true);
  if (error) throw new Error('error');
  return (
    <Button
      danger
      type="primary"
      onClick={() => {
        setError(true);
      }}
    >
      hola
    </Button>
  );
};

function App() {
  const [pathname, setPathname] = useState('/default');
  const webcamRef = useRef<Webcam>(null);
  const [datoObtenidos, setdatoObtenidos] = useState('')
  const [loading, setloading] = useState(false)
  const [chagpt, setchagpt] = useState('')
  const captureImage = () => {
    const imageSrc = webcamRef.current?.getScreenshot();
    if (imageSrc) {
      performOCR(imageSrc);
    }
  };
  const performOCR = async (imageBase64: string) => {
    ApiGo(imageBase64)
  };




  const ApiGo = (imageBase64: any) => {
    setloading(true)
    const apiKey = "AIzaSyDm6_GQQuSfib4cabezn6VUU7iK75GlNqI"; // Reemplazar con tu clave de API


    var base = imageBase64.replace("data:image/jpeg;base64,", "");
    const apiUrl =
      "https://vision.googleapis.com/v1/images:annotate?key=" + apiKey;
    const request = {
      requests: [
        {
          image: {
            content: imageBase64.replace("data:image/jpeg;base64,", ""),
          },
          features: [
            {
              type: "TEXT_DETECTION",
            },
          ],
        },
      ],
    };


    axios({
      method: "post",
      url: apiUrl,
      data: request,
      headers: { "Content-Type": "application/json" },
    })
      .then(function (response) {
        const ocrResult = response.data.responses[0].fullTextAnnotation.text;
        setdatoObtenidos(ocrResult)
        ResolveProblem(ocrResult)
        setloading(false)
      })
      .catch(function (response) { console.log(JSON.stringify(response)) });
  }

  const ResolveProblem = (texto: any) => {
    setloading(true)
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append("Authorization", "Bearer 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");

    var raw = JSON.stringify({
      "model": "gpt-3.5-turbo",
      "messages": [
        {
          "role": "user",
          "content": texto
        }
      ]
    });

    var requestOptions: any = {
      method: 'POST',
      headers: myHeaders,
      body: raw,
      redirect: 'follow'
    };

    fetch("https://api.openai.com/v1/chat/completions", requestOptions)
      .then(response => response.text())
      .then(result => setchagpt(result)
      )
      .catch(error => console.log('error', error));
    setloading(false)
  }
  return (
    <div
      id="test-pro-layout"
      style={{
        height: '100vh',
      }}
    >
      <ProLayout
        title="Tarea Final UMG"
        logo="https://umg.edu.gt/assets/umg.png"
        avatarProps={{
          src: 'https://gw.alipayobjects.com/zos/antfincdn/efFD%24IOql2/weixintupian_20170331104822.jpg',
          size: 'large',
          title: 'Javier Gomez Riz',

        }}
        route={{
          path: '/',
          routes: [
            {
              path: '/',
              name: 'ayuda',
            },

          ],
        }}
        location={{
          pathname,
        }}
        menuItemRender={(item, dom) => (
          <a
            onClick={() => {
              setPathname(item.path || '/welcome');
            }}
          >
            {dom}
          </a>
        )}
        ErrorBoundary={pathname === '/custom' ? "" : undefined}
      >
        <PageContainer>
          {loading ? (<>
            <img
              width={256}
              src="https://gw.alipayobjects.com/zos/antfincdn/zIgkN%26mpMZ/shibaizhuangtaizuo.png"
            />
            <h3 style={{
              maxWidth: 620,
              color: "#303F9F",
              textAlign: 'center',
              padding: '20px 45px 20px 26px',
              margin: 'auto',
              marginBlockEnd: '30px',
              marginBlockStart: '20px',
            }}>ANALIZANDO ESPERE</h3>
            <ProSkeleton type="result" />
          </>) : (<>
            <ProCard
              title=""
              extra="Camara"
              tooltip="Ayuda"
              style={{ maxWidth: 1200 }}
              headerBordered
            >
              <div style={{
                maxWidth: 620,
                textAlign: 'start',
                padding: '20px 45px 20px 26px',
                margin: 'auto',
                marginBlockEnd: '30px',
                marginBlockStart: '20px',
              }}>
                <Webcam audio={false} ref={webcamRef} screenshotFormat="image/jpeg" />
                <div style={{
                  maxWidth: 620,
                  textAlign: 'center',
                  padding: '20px 0px 30px 150px',
                  margin: 'auto',
                  marginBlockEnd: '30px',
                  marginBlockStart: '20px',
                }}>
                  <Button onClick={captureImage} type="primary" block>
                    Capturar
                  </Button>
                </div>

              </div>
              <div
                style={{
                  maxWidth: "100%",
                  textAlign: 'start',
                  backgroundColor: 'rgba(255,229,100,0.3)',
                  borderInlineStartColor: '#ffe564',
                  borderInlineStartWidth: '9px',
                  borderInlineStartStyle: 'solid',
                  padding: '5px 0px 5px 10px',
                  margin: 'auto',
                  marginBlockEnd: '30px',
                  marginBlockStart: '20px',
                }}
              >
                <h3>{datoObtenidos}</h3>
              </div>
              <h3>RESULTADOS</h3>
              <div
                style={{
                  maxWidth: "100%",
                  textAlign: 'start',
                  backgroundColor: 'rgba(255,229,100,0.3)',
                  borderInlineStartColor: '#ffe564',
                  borderInlineStartWidth: '9px',
                  borderInlineStartStyle: 'solid',
                  padding: '5px 0px 5px 10px',
                  margin: 'auto',
                  marginBlockEnd: '30px',
                  marginBlockStart: '20px',
                }}
              >
                <h3>{chagpt}</h3>
              </div>
              <Button
                danger
                type="primary"
                onClick={() => {
                  window.location.reload();
                }}
              >
                Finalizar
              </Button>
            </ProCard>
          </>)}



          {/* }
          /> */}


        </PageContainer>
      </ProLayout>
    </div>
  );
}

export default App;
